.page-header {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 56px;
  grid-row: 1;
  grid-column: 1 / -1;

  @media (min-width: $tablet-width) {
    grid-template-rows: 56px;
  }
}

.page-header__toggle {
  grid-row: 1;
  grid-column-end: -1;
  justify-self: end;
  align-self: start;
  height: 20px;
  width: 20px;
  margin: 18px 21px;
  background: transparent;
  cursor: pointer;
  z-index: 5;

  @media (min-width: $tablet-width) {
    display: none;
  }

  &::before {
    content: "";
    position: absolute;
    top: 17px;
    right: 20px;
    width: 23px;
    height: 4px;
    background-color: $menu-background;
    box-shadow: 0 9px 0 0 $menu-background, 0 18px 0 0 $menu-background;
  }
}

.page-header--opened .page-header__toggle::after,
.page-header--opened .page-header__toggle::before {
  content: "";
  position: absolute;
  top: 26px;
  right: 17px;
  width: 28px;
  height: 4px;
  box-shadow: none;
  background-color: $body-background;
  border-radius: 1px;
}

.page-header--opened .page-header__toggle::before {
  transform: rotate(45deg);
}

.page-header--opened .page-header__toggle::after {
  transform: rotate(-45deg);
}

.page-header__logo {
  justify-self: center;
  align-self: start;
  z-index: 1;

  @media (min-width: $tablet-width) {
    grid-row: 1;
    grid-column: 1 / -1;
  }

  @media (min-width: $desktop-width) {
    width: 187px;
  }
}

.page-header__logo-image {
  width: 100%;
}
