.hero {
  position: relative;
  height: 389px;

  @media (min-width: $tablet-width) {
    height: 518px;
  }

  &--small {
    height: 158px;

    @media (min-width: $tablet-width) {
      height: 290px;
    }

    @media (min-width: $desktop-width) {
      height: 301px;
    }
  }
}

.hero__background {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 20%;

  &--bottom {
    object-position: bottom;
  }
}

.hero__background-text {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;

  &--large {
    top: 90px;

    @media (min-width: $tablet-width) {
      top: 77px;
      width: 460px;
      height: 350px;
    }
  }

  &--small {
    top: 52px;

    @media (min-width: $tablet-width) {
      top: 95px;
      width: 457px;
      height: 78px;
    }

    @media (min-width: $desktop-width) {
      top: 101px;
      width: 460px;
      height: 80px;
    }
  }
}

.hero__triangle {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  width: 100%;
}
